const FilterColumn_4 = (props: audienceProp) :React.ReactElement<Props>  =>  {
    const classes = useStyles()
    const dispatch = useDispatch()
    const theme = useTheme()

    const optionSelector:any = useSelector((state: RootState) => state.audienceFilterSelecetor)
    const matchesMac = useMediaQuery(theme.breakpoints.down('xl'));
    const [collapse, setCollapse] = React.useState(true)

    const data = props.data
    const group = props.group
    const chooseValue = props.chooseValue
    const chooseSubValue = props.chooseSubValue
    const filterKey = props.filterKey

    return(
        <Grid key={group[filterKey][0].filterId} container alignItems={'center'} className={classes.filterPannel}>
            <Grid item style={{width:'100%'}}>
                {
                    group[filterKey].map((filter: filterColumn, index:number) =>
                    {
                        return (
                            <>
                                <Grid container justifyContent={'space-between'} alignItems={'center'} style={{marginBottom:'10px'}}>
                                    <Grid item>
                                        <Grid container alignItems={'flex-start'}>
                                            {index === 0 ?
                                                <Grid item style={{ width: matchesMac ? '130px' : '200px', marginRight: '10px' }}>
                                                    <FormControl fullWidth>
                                                        <Select
                                                            disableUnderline
                                                            variant='standard'
                                                            style={{padding:'0px 10px', borderRadius:5, border:'1px solid #5A91FF'}}
                                                            labelId="demo-simple-select-label"
                                                            id="demo-simple-select"
                                                            className={classes.columnSelected}
                                                            inputProps={{
                                                                id: 'uncontrolled-native'
                                                            }}
                                                            value={filter.value}
                                                            disabled={!props.editable}
                                                            renderValue={(value) =>
                                                            {
                                                                const mappingsName = optionSelector[data.value].filter((x:any)=> x.value === value)[0]?.name
                                                                return mappingsName
                                                            }}
                                                            onChange={(event)=> dispatch(handleAudienceChangeForNameMutidimension(data, filter, optionSelector[data.value].filter((x:any)=> x.value === event.target.value)[0]))}
                                                        >
                                                            {optionSelector[data.value].filter((x:any)=> _.includes(chooseValue,x.value) === false).map((option:any) =>
                                                            {
                                                                return  <MenuItem key={option.id} value={option.value} > <Typography style={{fontSize:matchesMac? '14px':'16px'}}>{option.name}</Typography></MenuItem>
                                                            })}
                                                        
                                                        </Select>
                                                    </FormControl>
                                                </Grid>
                                                : <Grid item style={{ width: matchesMac ? '130px' : '200px', marginRight: '10px' }}></Grid>
                                            }
                                            <Grid item style={{ width: matchesMac ? '130px' : '200px', marginRight: '10px' }}>
                                                <FormControl fullWidth>
                                                    <Select
                                                        disableUnderline
                                                        variant='standard'
                                                        style={{padding:'0px 10px', borderRadius:5, border:'1px solid #626262'}}
                                                        labelId="demo-simple-select-label"
                                                        id="demo-simple-select"
                                                        className={classes.columnSelected}
                                                        inputProps={{
                                                            id: 'uncontrolled-native'
                                                        }}
                                                        value={filter.subValue}
                                                        disabled={!props.editable}
                                                        renderValue={(value) =>
                                                        {
                                                            const mappingsName = optionSelector[data.value].filter((x: any) => x.value === filter.value)[0]?.subname[value]?.name
                                                            return mappingsName
                                                        }}
                                                        onChange={(event)=> dispatch(handleAudienceChangeForSubNameMutidimension(data, filter, event.target.value))}
                                                    >
                                                        {_.keys(optionSelector[data.value].filter((x:any)=> x.value === filter.value)[0]?.subname).map( (subname:any) =>
                                                        {
                                                            const item = optionSelector[data.value].filter((x: any) => x.value === filter.value)[0].subname[subname]
                                                            if (_.includes(chooseSubValue, item.value) === false)
                                                            {
                                                                return  <MenuItem key={item.id} value={item.value} > <Typography style={{fontSize:matchesMac? '14px':'16px'}}>{item.name}</Typography></MenuItem>
                                                            }
                                                            
                                                        })}
                                                    
                                                    </Select>
                                                </FormControl>
                                            </Grid>
                                            {
                                                filter.type === 'boolean' ? '' : 
                                                <Grid item style={{width:matchesMac?'110px':'200px',marginRight:'10px'}}>
                                                    <FormControl fullWidth>
                                                        <Select
                                                            disableUnderline
                                                            variant='standard'
                                                            style={{padding:'0px 10px', borderRadius:5, border:'1px solid #626262'}}
                                                            labelId="demo-simple-select-label"
                                                            id="demo-simple-select"
                                                            className={classes.operactorSelected}
                                                            inputProps={{
                                                                id: 'uncontrolled-native',
                                                            }}
                                                            value={filter.operator}
                                                            disabled={!props.editable}
                                                            onChange={(event)=> dispatch(handleAudienceChangeForOperatorMutidimension(data, filter,  event.target.value))}
                                                        >
                                                            {
                                                                _.keys(optionSelector[data.value].filter((x:any)=> x.value === filter.value)[0]?.subname[filter.subValue]?.operator).map( (operator:any) =>
                                                                {
                                                                    const item = optionSelector[data.value].filter((x: any) => x.value === filter.value)[0]?.subname[filter.subValue]?.operator[operator]
                                                                    return (
                                                                        <MenuItem key={item.name} value={item.value} > <Typography style={{fontSize:matchesMac? '14px':'16px'}}>{item.name}</Typography></MenuItem>
                                                                    )
                                                                })
                                                            }
                                                        </Select>
                                                    </FormControl>
                                                </Grid>
                                            }
                                            {
                                                ColumnTypeGenerator_4(data, filter, optionSelector,classes, dispatch, props.editable)
                                            }
                                        </Grid>
                                    </Grid>
                                    <Grid item>
                                        <Grid container alignItems={'center'}>
                                            <IconButton disabled={!props.editable} style={{padding:6}} onClick={()=> dispatch(handleAudienceChangeCopyFilterButton(data,filter.filterId))}>
                                                <FilterNoneIcon style={{color:'#626262',width:'28px',height:'28px'}}/>
                                            </IconButton>
                                            <IconButton disabled={!props.editable} style={{padding:6}} onClick={()=> dispatch(handleAudienceChangeRemoveFilterButton(data,filter.filterId))}>
                                                <DeleteIcon style={{color:'#626262',width:'28px',height:'28px'}}/>
                                            </IconButton>
                                        </Grid>
                                    </Grid>
                                </Grid>
                                {
                                    index === group[filterKey].length - 1 ? 
                                        <Grid item container alignItems={'center'} style={{marginTop:'-10px'}}>
                                            <Grid item style={{width:matchesMac? '130px' : '200px'}}>
                                            </Grid>
                                            <Grid item>
                                                <Button disabled={!props.editable} onClick={()=>dispatch(handleAudienceChangeRefineFilterButton(data,filter.filterId))} startIcon={<AddIcon/>} disableRipple  className={classes.refineButton} style={{backgroundColor:'transparent',textTransform:'none',color:'#5A91FF',fontSize:'16px',marginTop:'10px'}} >
                                                    {'Refine more'}
                                                </Button>
                                            </Grid>
                                        </Grid>
                                    : ''
                                }
                            </>
                            
                        )
                    })
                }
                
            </Grid>
        </Grid>
    )
}

export default FilterColumn_4