import React, { useEffect, useLayoutEffect, useRef, useState } from 'react';
import { styled } from '@mui/material/styles';
import { useTheme } from '@mui/material/styles';
import useMediaQuery from '@mui/material/useMediaQuery';
import { useSelector } from 'react-redux'
import { useDispatch } from 'react-redux'
import { RootState } from '../../reducers/index'
import {
    InputAdornment,
    Typography,
} from '@material-ui/core';
import Grid from '@mui/material/Grid';
import Collapse from '@mui/material/Collapse';
import ArrowDropDownCircleIcon from '@mui/icons-material/ArrowDropDownCircle';
import ArrowDropUpCircleIcon from '@mui/icons-material/ArrowDropUp';
import { makeStyles, createStyles, Theme } from '@material-ui/core/styles';
import _ from 'lodash'
import RemoveCircleOutlineIcon from '@mui/icons-material/RemoveCircleOutline';
import DragIndicatorIcon from '@mui/icons-material/DragIndicator';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import Select from '@mui/material/Select';
import TextField from '@mui/material/TextField';
import Button from '@mui/material/Button';
import AddIcon from '@mui/icons-material/Add';
import FilterNoneIcon from '@mui/icons-material/FilterNone';
import DeleteIcon from '@mui/icons-material/Delete';
import { IconButton } from '@mui/material';
import LabelIcon from '@mui/icons-material/Label';
import LinearProgress, { linearProgressClasses } from '@mui/material/LinearProgress';
import { PieChart, Pie, Label, Cell, ResponsiveContainer } from 'recharts';
import { handleAudienceRemoveFilter } from '../../actions/audienceAction'
import  Autocomplete  from '@mui/material/Autocomplete';
import { creatorType, filterColumn } from '../../reducers/audienceReducer'
import TextFieldsIcon from '@mui/icons-material/TextFields';
import moment from 'moment';
import ClearIcon from '@mui/icons-material/Clear';
import
{
    handleAudienceChangeForValues,
} from '../../actions/audienceAction'
import Paper from "@material-ui/core/Paper";


interface BadgeProps {
    label: string
    color: string
    data: creatorType
    filter: filterColumn
    exceptValues: string[]
}

const MutipleSelectBadge = (props: BadgeProps) =>
{
    const dispatch = useDispatch()
    return (
        <Grid item  style={{ marginRight:'10px',padding: '2px 8px', margin:'4px',  borderRadius: '5px',  backgroundColor: props.color, border:'1px solid #5A91FF' }}>
            <Grid container justifyContent='space-between' alignItems='center' style={{width: '100%', height: '18px'}}>
                <Typography style={{ fontFamily: 'Noto sans', color: '#000', fontSize: '12px' }}>{props.label}</Typography>
                <IconButton onClick={(event) => { event.stopPropagation(); dispatch(handleAudienceChangeForValues(props.data, props.filter,  props.exceptValues))}} style={{ padding: 0 }}><ClearIcon style={{fill:'#000',width:'12px',height:'12px',marginLeft:'4px'}}/></IconButton>
            </Grid>
        </Grid>
    )
}

const RenderValueItem = (data: creatorType, filter: filterColumn, optionsSelector: any, classes: any, dispatch: any, editable: boolean) =>
{
    switch (filter.type)
    {
        case 'string': 
            return <Grid item style={{ width: '220px', marginRight: '10px' }}>
                <TextField
                    type='input'
                    variant='standard'
                    inputProps={
                        {
                            style: { width: '130px', height: '30px', padding: '0px 0px 0px 15px',border: 'none',backgroundColor:'#F3F3F3'},
                            underline: {
                                "&&&:before": {
                                    borderBottom: "none"
                                },
                                "&&:after": {
                                    borderBottom: "none"
                                }
                            }
                        }}
                    InputProps={{
                        startAdornment: <TextFieldsIcon style={{ width: '18px', height: '18px' }} />,
                        disableUnderline: true
                    }}
                    style={{backgroundColor:'#F3F3F3',padding:'0px 10px',borderRadius:5}}
                    placeholder='Enter value'
                    value={filter.optionValues.value1}
                    disabled={!editable} 
                    onChange={(event) => dispatch(handleAudienceChangeForValues(data, filter,  event.target.value))}
                />
            </Grid>
        case 'number':
            return<Grid item style={{ width: '220px', marginRight: '10px' }}>
                <TextField
                    placeholder='Enter value'
                    type='number'
                    disabled={!editable}
                    variant='standard'
                    value={filter.optionValues.value1}
                    onChange={(event) => dispatch(handleAudienceChangeForValues(data, filter,  event.target.value))}
                    inputProps={
                        {
                            style: { width: '130px', height: '30px', padding: '0px 0px 0px 15px',border: 'none',backgroundColor:'#F3F3F3' },
                            underline: {
                                "&&&:before": {
                                    borderBottom: "none"
                                },
                                "&&:after": {
                                    borderBottom: "none"
                                }
                            }
                        }}
                    InputProps={{
                        disableUnderline: true
                    }}
                    style={{backgroundColor:'#F3F3F3',padding:'0px 5px',borderRadius:5}}
                    >
                    </TextField>
            </Grid>
        
        case 'mutipleNumber':
            return ( <Grid item>
                <Grid container alignItems={'center'}>
                    <Grid item style={{  marginRight: '10px' }}>
                        <TextField
                            placeholder='Enter value'
                            type='number'
                            value={filter.optionValues.value1}
                            onChange={(event) => dispatch(handleAudienceChangeForValues(data, filter,  event.target.value, 0))}
                            disabled={!editable}
                            variant='standard'
                            inputProps={
                                {
                                    style: { width: '130px', height: '30px', padding: '0px 0px 0px 15px',border: 'none',backgroundColor:'#F3F3F3' },
                                    underline: {
                                        "&&&:before": {
                                            borderBottom: "none"
                                        },
                                        "&&:after": {
                                            borderBottom: "none"
                                        }
                                    }
                                }}
                            InputProps={{
                                disableUnderline: true
                            }}
                            style={{backgroundColor:'#F3F3F3',paddingLeft:'5px',borderRadius:5}}
                            >
                        </TextField>
                    </Grid>
                    <Grid item style={{  marginRight: '10px' }}>
                        <Typography>{'to'}</Typography>
                    </Grid>
                    <Grid item style={{ width: '220px', marginRight: '10px' }}>
                        <TextField
                            placeholder='Enter value'
                            type='number'
                            disabled={!editable}
                            value={filter.optionValues.value2}
                            onChange={(event) => dispatch(handleAudienceChangeForValues(data, filter,  event.target.value,1))}
                            variant='standard'
                            inputProps={
                                {
                                    style: { width: '130px', height: '30px', padding: '0px 0px 0px 15px',border: 'none',backgroundColor:'#F3F3F3' },
                                    underline: {
                                        "&&&:before": {
                                            borderBottom: "none"
                                        },
                                        "&&:after": {
                                            borderBottom: "none"
                                        }
                                    }
                                }}
                            InputProps={{
                                disableUnderline: true
                            }}
                            style={{backgroundColor:'#F3F3F3',paddingLeft:'5px',borderRadius:5}}
                            >
                        </TextField>
                    </Grid>
                </Grid>
            </Grid>
        )
        case 'boolean':
            const options_bool:any = [
                { id: 0, name: 'True', value: 'true' },
                { id: 1, name: 'False', value: 'false' }
            ]
            return (
                <Grid item>
                    <Grid container alignItems={'center'}>
                        <Grid item style={{marginRight:'10px'}}>
                            <Typography>{'is'}</Typography>
                        </Grid>
                        <Grid item>
                            <FormControl fullWidth>
                                <Select
                                    disableUnderline
                                    variant='standard'
                                    style={{padding:'0px 10px', borderRadius:5, border:'1px solid #626262'}}
                                    labelId="demo-simple-select-label"
                                    id="demo-simple-select"
                                    inputProps={{
                                        id: 'uncontrolled-native',
                                    }}
                                    value={filter.optionValues.value1}
                                    disabled={!editable}
                                    onChange={(event) => dispatch(handleAudienceChangeForValues(data, filter,  event.target.value))}
                                >
                                    {
                                        options_bool.map((option:any)=>
                                        {
                                            return <MenuItem key={option.id} value={option.value}> <Typography style={{fontSize:'16px'}}>{option.name}</Typography></MenuItem>
                                        })
                                    }
                                
                                </Select>
                            </FormControl>
                        </Grid>
                    </Grid>
                </Grid>
            )
        case 'datetime':
            return <Grid item >
                    <TextField
                        variant="outlined"
                        id="date"
                        type="date"
                        lang="en-US"
                        InputLabelProps={{
                        shrink: true,
                        }}
                        value={filter.optionValues.value1}
                        disabled={!editable} 
                        onChange={(event) => dispatch(handleAudienceChangeForValues(data, filter,  event.target.value))}
                        inputProps={{style:{width:'140px',height:'30px',padding:'0px 0px 0px 15px',border: '1px solid #626262',borderRadius:5}}}
                    />
            </Grid>
        case 'mutipleDatetime':
            return <Grid item >
                <Grid container alignItems={'center'}>
                    <Grid item>
                        <TextField
                            variant="outlined"
                            id="date"
                            type="date"
                            InputLabelProps={{
                            shrink: true,
                            }}
                            value={filter.optionValues.value1}
                            disabled={!editable} 
                            onChange={(event) => dispatch(handleAudienceChangeForValues(data, filter,  event.target.value,0))}
                            inputProps={{style:{width:'140px',height:'30px',padding:'0px 0px 0px 15px',border: '1px solid #626262',borderRadius:5}}}
                        />
                    </Grid>
                    <Grid item style={{margin:'0px 10px'}}>
                        <Typography>{'to'}</Typography>
                    </Grid>
                    <Grid item>
                        <TextField
                            variant="outlined"
                            id="date"
                            type="date"
                            lang="en-US"
                            InputLabelProps={{
                            shrink: true,
                            }}
                            value={filter.optionValues.value2}
                            disabled={!editable} 
                            onChange={(event) => dispatch(handleAudienceChangeForValues(data, filter,  event.target.value,1))}
                            inputProps={{style:{width:'140px',height:'30px',padding:'0px 0px 0px 15px',border: '1px solid #626262',borderRadius:5}}}
                        />
                    </Grid>
                </Grid>
            </Grid>
        case 'select':
            const options = _.keys(optionsSelector[data.value].filter((x: any) => x.value === filter.value)[0]?.operator[filter.operator]?.options)
            return <Grid item style={{ width:'200px', marginRight: '10px' }}>
                <FormControl fullWidth>
                    <Autocomplete
                        id="tags-standard"
                        options={options}
                        getOptionLabel={(option) =>
                        {
                            const item = optionsSelector[data.value].filter((x: any) => x.value === filter.value)[0]?.operator[filter.operator]?.options[option]
                            if (item === undefined)
                            {
                                return ''    
                            }
                            return item.name
                        }}
                        defaultValue={[options[0]]}
                        disableCloseOnSelect
                        value={filter.optionValues.value1}
                        ListboxProps={{ style: { maxHeight: '12rem' } }}
                        disabled={!editable} 
                        onChange={(event,values) => dispatch(handleAudienceChangeForValues(data, filter,  values))}
                        style={{ padding: '0px 10px', borderRadius: 5, border: '1px solid #626262' }}
                        renderInput={(params) => (
                        <TextField
                            {...params}
                            variant='standard'
                            InputProps={{ ...params.InputProps, disableUnderline: true }}
                        />
                        )}
                    />
                    
                </FormControl>
            </Grid>
        case 'mutipleSelect':
            const mutipleOptions = _.keys(optionsSelector[data.value].filter((x: any) => x.value === filter.value)[0]?.operator[filter.operator]?.options)
            return ( <Grid item style={{ marginRight: '20px' }}>
                <FormControl fullWidth>
                    <Autocomplete
                        multiple
                        id="tags-standard"
                        options={mutipleOptions}
                        getOptionLabel={(option) =>
                        {
                            const item = optionsSelector[data.value].filter((x: any) => x.value === filter.value)[0]?.operator[filter.operator]?.options[option]
                            if (item === undefined)
                            {
                                return '' 
                            }
                            return item.name
                        }}
                        defaultValue={[mutipleOptions[0]]}
                        disableCloseOnSelect
                        value={typeof(filter.optionValues.value1) === 'object' ?  filter.optionValues.value1 : []}
                        disabled={!editable} 
                        ListboxProps={{ style: { maxHeight: '12rem' } }}
                        onChange={(event,values) => dispatch(handleAudienceChangeForValues(data, filter,  values))}
                        style={{paddingLeft: '10px', width: '280px', color: '#0E0F0F', fontSize: '14px', backgroundColor: '#EDEDED', fontFamily: 'Noto Sans', borderRadius: 5 }}
                        renderTags={(values) =>
                        {
                            const items = values.map((x: any) => optionsSelector[data.value].filter((x: any) => x.value === filter.value)[0]?.operator[filter.operator]?.options[x])
                            return (
                                items?.map((item:any) =>
                                {
                                    if (item === undefined)
                                    {
                                        return '' 
                                    }
                                    return <MutipleSelectBadge label={item.name} color={'#E1EFFF'} data={data} filter={filter} exceptValues={filter.optionValues.value1.filter((x:any)=>x!== item.value)}/>
                                })
                            )
                        }}
                        renderInput={(params) => (
                        <TextField
                            {...params}
                            variant="standard"
                            placeholder={filter.optionValues.value1?.length > 0 ? '' : "Add tags"}
                            InputProps={{ ...params.InputProps, disableUnderline: true,startAdornment: (
                                <>
                                <InputAdornment position="start">
                                    <LabelIcon fontSize={'small'} style={{color:'#000'}}/>
                                </InputAdornment>
                                {params.InputProps.startAdornment}
                                </>
                            ) }}
                        />
                        )}
                    />
                </FormControl>
            </Grid>)
        case 'empty':
            return <Grid></Grid>
    }
}

export default RenderValueItem