const RenderValueItem = (data: creatorType, filter: filterColumn, optionsSelector: any, classes: any, dispatch: any, props: audienceProp) =>
{
    switch (filter.type)
    {
        case 'string': 
            return <Grid item style={{ width: '220px', marginRight: '10px' }}>
                <TextField
                    type='input'
                    variant="outlined"
                    inputProps={{ style: { width: '130px', height: '30px', padding: '0px 0px 0px 15px', border: '1px solid #626262', borderRadius: 5 } }}
                    value={filter.optionValues.value1}
                    disabled={!props.editable} 
                    onChange={(event) => dispatch(handleAudienceChangeForValues(data, filter,  event.target.value))}
                />
            </Grid>
        case 'number':
            return <Grid item style={{ width: '220px', marginRight: '10px' }}>
                <TextField type='number' variant="outlined"  inputProps={{style:{width:'130px',height:'30px',padding:'0px 0px 0px 15px',border: '1px solid #626262',borderRadius:5}}} ></TextField>
            </Grid>
        case 'datetime':
            return <Grid item >
                    <TextField
                        variant="outlined"
                        id="date"
                        type="date"
                        lang="en-US"
                        InputLabelProps={{
                        shrink: true,
                        }}
                        value={filter.optionValues.value1}
                        disabled={!props.editable} 
                        onChange={(event) => dispatch(handleAudienceChangeForValues(data, filter,  event.target.value))}
                        inputProps={{style:{width:'140px',height:'30px',padding:'0px 0px 0px 15px',border: '1px solid #626262',borderRadius:5}}}
                    />
            </Grid>
        case 'mutipleDatetime':
            return <Grid item >
                <Grid container alignItems={'center'}>
                    <Grid item>
                        <TextField
                            variant="outlined"
                            id="date"
                            type="date"
                            InputLabelProps={{
                            shrink: true,
                            }}
                            value={filter.optionValues.value1}
                            disabled={!props.editable} 
                            onChange={(event) => dispatch(handleAudienceChangeForValues(data, filter,  event.target.value,0))}
                            inputProps={{style:{width:'140px',height:'30px',padding:'0px 0px 0px 15px',border: '1px solid #626262',borderRadius:5}}}
                        />
                    </Grid>
                    <Grid item style={{margin:'0px 10px'}}>
                        <Typography>{'to'}</Typography>
                    </Grid>
                    <Grid item>
                        <TextField
                            variant="outlined"
                            id="date"
                            type="date"
                            lang="en-US"
                            InputLabelProps={{
                            shrink: true,
                            }}
                            value={filter.optionValues.value2}
                            disabled={!props.editable} 
                            onChange={(event) => dispatch(handleAudienceChangeForValues(data, filter,  event.target.value,1))}
                            inputProps={{style:{width:'140px',height:'30px',padding:'0px 0px 0px 15px',border: '1px solid #626262',borderRadius:5}}}
                        />
                    </Grid>
                </Grid>
            </Grid>
        case 'select':
            const options = _.keys(optionsSelector[data.value].filter((x: any) => x.value === filter.value)[0]?.operator[filter.operator]?.options)
            return <Grid item style={{ width:'200px', marginRight: '10px' }}>
                <FormControl fullWidth>
                    <Select
                        labelId="demo-simple-select-label"
                        id="demo-simple-select"
                        className={classes.operactorSelected}
                        inputProps={{
                            id: 'uncontrolled-native',
                        }}
                        value={filter.optionValues.value1}
                        disabled={!props.editable} 
                        onChange={(event) => dispatch(handleAudienceChangeForValues(data, filter,  event.target.value))}
                    >
                        {
                            options.map((option:any)=>
                            {
                                const item = optionsSelector[data.value].filter((x: any) => x.value === filter.value)[0]?.operator[filter.operator]?.options[option]
                                return <MenuItem key={item.id} value={item.value}> <Typography style={{fontSize:'16px'}}>{item.name}</Typography></MenuItem>
                            })
                        }
                    
                    </Select>
                </FormControl>
            </Grid>
        case 'mutipleSelect':
             const mutipleOptions = _.keys(optionsSelector[data.value].filter((x: any) => x.value === filter.value)[0]?.operator[filter.operator]?.options)

            return ( <Grid item style={{ marginRight: '20px' }}>
                <FormControl fullWidth>
                    <Select
                        startAdornment={
                            <InputAdornment position="start">
                                <LabelIcon fontSize={'small'} style={{color:'#000'}}/>
                            </InputAdornment>
                        }
                        IconComponent={() => (
                            <React.Fragment></React.Fragment>
                        )}
                        variant='standard'
                        disableUnderline
                        labelId="demo-multiple-name-label"
                        id="demo-multiple-name"
                        value={typeof(filter.optionValues.value1) === 'object' ?  filter.optionValues.value1 : []}
                        disabled={!props.editable} 
                        onChange={(event) => dispatch(handleAudienceChangeForValues(data, filter,  event.target.value))}
                        multiple
                        style={{paddingLeft:'10px',width:'250px',height:'32px',color:'#0E0F0F',fontSize:'14px',backgroundColor:'#EDEDED', fontFamily:'Noto Sans',borderRadius:5}}
                        MenuProps={MenuProps}
                        renderValue={(values) =>
                        {
                            const names = values.map((x:any) => optionsSelector[data.value].filter((x: any) => x.value === filter.value)[0]?.operator[filter.operator]?.options[x]?.name)
                            return _.join(names,',')
                        }}
                    >
                        {
                            mutipleOptions.map((option:any)=>
                            {
                                const item = optionsSelector[data.value].filter((x: any) => x.value === filter.value)[0]?.operator[filter.operator]?.options[option]
                                return <MenuItem key={item.id} value={item.value}> <Typography style={{fontSize:'16px'}}>{item.name}</Typography></MenuItem>
                            })
                        }
                    </Select>
                </FormControl>
            </Grid>)
    }
}

const RenderFilterColumn = (data: creatorType, props: audienceProp) =>
{
    const dimension3:filterColumn[] = []
    const dimension4:filterColumn[] = []
    data.filterColumn?.map((filter:filterColumn, index:number) =>
    {
        const di = audienceSelectorMapping[data.value].filter((x: any) => x.value === filter.value)[0]?.dimension
        const dimension = di ? di : 3
        if (dimension === 3)
        {
            dimension3.push(filter)
        }
        else 
        {
            dimension4.push(filter)
        }
    })

    const viewDimenstion3 = dimension3.map((filter:filterColumn, index:number) =>
        {
            const chooseValue = dimension3.map(x => x.value)
            return (
                <FilterColumn_3 key={index} data={data} chooseValue={chooseValue} filter={filter} editable={props.editable} />
            )
    })
    
    const group = _.groupBy(dimension4, 'value')
    const viewDimenstion4 = _.keys(group).map((filterKey:string, index:number) =>
    {
        const chooseValue = data.filterColumn.map(x => x.value)
        const chooseSubValue = data.filterColumn.filter(x => x.value === filterKey).map(x => x.subValue)
        return (
            <FilterColumn_4 key={index} data={data} chooseValue={chooseValue} chooseSubValue={chooseSubValue} group={group} filterKey={filterKey} editable={props.editable} />
        )
    })

    return (
        <>
            {viewDimenstion3}
            {viewDimenstion4}
        </>
    )
}

